<template>
  <v-app>
    <nav>
      <Navbar main="tes main">
        <template v-slot:back>
          <v-btn small fab text color="primary_red_1" @click="$router.push('/featured')">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
        </template>
        <template v-slot:body>
          <v-toolbar-title>Dealer Detail</v-toolbar-title>
        </template>
      </Navbar>
    </nav>
    <v-content v-if="isLoading">
      <v-container>
        <v-row>
          <v-col v-for="item in skeletonLoaderItem" :key="item" cols="12" sm="6" md="4" lg="3" xl="3">
            <v-skeleton-loader type="list-item-avatar-three-line"></v-skeleton-loader>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-content v-if="!isLoading">
      <v-container>
        <v-alert
          style="z-index:1000"
          v-model="success"
          :value="success"
          transition="scale-transition"
          dismissible
          type="success"
          >{{ successMessage }}</v-alert
        >
        <v-alert
          style="z-index:1000"
          v-model="error"
          :value="error"
          transition="scale-transition"
          dismissible
          type="error"
          >{{ errorMessage }}</v-alert
        >

        <v-list v-for="(dealerDetail, name) in dealer" :key="name" subheader three-line>
          <v-list-item v-if="name != 'image'">
            <v-list-item-content>
              <v-list-item-subtitle>{{ name }}</v-list-item-subtitle>
              <v-list-item-title>{{ dealerDetail }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <div v-if="name == 'image'">
            <div>{{ name }}</div>
            <v-avatar class="mr-2">
              <v-img v-if="name != 'Total'" :src="apiImage(dealerDetail)"></v-img>
            </v-avatar>
          </div>
        </v-list>

        <!-- <v-row>
          <v-col>{{this.dealer.name}}</v-col>
          <v-col>{{this.dealer.email}}</v-col>
          <v-col>{{this.dealer.phone}}</v-col>
          <v-col>
            <v-avatar>
              <v-img :src="apiImage(this.dealer.image)"></v-img>
            </v-avatar>
          </v-col>
        </!-->

        <!-- data table store ================================================= -->
        <template>
          <v-data-table :headers="storeHeaders" :items="stores" sort-by="index" class="elevation-1">
            <template v-slot:top>
              <v-card flat class="px-3 py-3">
                <v-row>
                  <v-col class="py-0" cols="12" lg="2" md="6" sm="12">
                    <div class="display-1">Store</div>
                  </v-col>
                  <v-col class="py-0" cols="12" lg="4" md="6" sm="12">
                    <v-text-field
                      v-on:keyup.13="searchDataTable"
                      outlined
                      v-model="search"
                      dense
                      color="primary_red_1"
                      label="Search"
                    >
                      <template v-slot:append>
                        <v-btn @click="searchDataTable" small text color="primary_grey_1">
                          <v-icon>mdi-magnify</v-icon>
                        </v-btn>
                      </template>
                    </v-text-field>
                  </v-col>
                  <v-col class="py-0" cols="12" lg="6" md="6" sm="12">
                    <v-layout justify-end>
                      <!-- <v-btn
                        class="ml-2 mb-1"
                        @click="openDialogImport"
                        color="primary_red_1"
                        dark
                        >import csv</v-btn
                      > -->
                      <v-btn color="primary_red_1" dark class="ml-2 mb-1" @click="dialog = true">New Item</v-btn>
                    </v-layout>
                  </v-col>
                </v-row>
              </v-card>
            </template>
            <template v-slot:item.index="{ item }">
              <div>{{ item.index + 1 }}</div>
            </template>
            <template v-slot:item.highestPrice="{ item }">
              <v-layout justify-end>
                {{
                  numberWithPeriod({
                    currency: 'Rp',
                    separator: '.',
                    number: item.highestPrice,
                  })
                }}
              </v-layout>
            </template>
            <template v-slot:item.action="{ item }">
              <div class="no-services">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on }">
                    <v-icon v-on="on" small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                  </template>
                  <span>Edit</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on }">
                    <v-icon v-on="on" small @click="deleteItem(item)">mdi-delete</v-icon>
                  </template>
                  <span>Delete</span>
                </v-tooltip>
              </div>
            </template>
            <template v-slot:item.image="{ item }">
              <v-avatar size="60">
                <v-img :src="apiImage(item.image)"></v-img>
              </v-avatar>
            </template>
          </v-data-table>
        </template>
        <!-- data table store ================================================= -->
        <v-row>
          <v-col></v-col>
        </v-row>
        <!-- data table address ================================================= -->
        <template>
          <v-data-table :headers="addressHeaders" :items="addresses" sort-by="index" class="elevation-1">
            <template v-slot:top>
              <v-card flat class="px-3 py-3">
                <v-row>
                  <v-col class="py-0" cols="12" lg="2" md="6" sm="12">
                    <div class="display-1">Address</div>
                  </v-col>
                  <v-col class="py-0" cols="12" lg="4" md="6" sm="12">
                    <v-text-field
                      v-on:keyup.13="searchDataTable"
                      outlined
                      v-model="search"
                      dense
                      color="primary_red_1"
                      label="Search"
                    >
                      <template v-slot:append>
                        <v-btn @click="searchDataTable" small text color="primary_grey_1">
                          <v-icon>mdi-magnify</v-icon>
                        </v-btn>
                      </template>
                    </v-text-field>
                  </v-col>
                  <v-col class="py-0" cols="12" lg="6" md="6" sm="12">
                    <v-layout justify-end>
                      <!-- <v-btn
                        class="ml-2 mb-1"
                        @click="openDialogImport"
                        color="primary_red_1"
                        dark
                      >import csv</v-btn> -->
                      <v-btn color="primary_red_1" dark class="ml-2 mb-1" @click="dialog = true">New Item</v-btn>
                    </v-layout>
                  </v-col>
                </v-row>
              </v-card>
            </template>
            <template v-slot:item.storeId.name="{ item }">
              <div>
                {{ item.storeId == null ? 'Belum ada toko' : item.storeId.name }}
              </div>
            </template>
            <template v-slot:item.index="{ item }">
              <div>{{ item.index + 1 }}</div>
            </template>
            <template v-slot:item.image="{ item }">
              <v-avatar size="60">
                <v-img :src="apiImage(item.image)"></v-img>
              </v-avatar>
            </template>
            <template v-slot:item.isMain="{ item }">
              <!-- <div>{{item.isMain}}</div> -->
              <v-chip
                @click="deleteItem(item)"
                class="mx-1"
                small
                :color="item.isMain == 'true' ? 'success' : 'primary_red_1'"
                dark
                >{{ item.isMain }}</v-chip
              >
            </template>
            <template v-slot:item.action="{ item }">
              <div class="no-services">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on }">
                    <v-icon v-on="on" small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                  </template>
                  <span>Edit</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on }">
                    <v-icon v-on="on" small @click="deleteItem(item)">mdi-delete</v-icon>
                  </template>
                  <span>Delete</span>
                </v-tooltip>
              </div>
            </template>
          </v-data-table>
        </template>
        <!-- data table address END ================================================= -->
        <v-row>
          <v-col></v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row v-if="image == null && editedIndex !== -1 && editedItem.image !== ''">
              <v-col class="px-10 pa-lg-5" cols="12" sm="12" md="12">
                <v-avatar size="100%">
                  <v-img
                    v-if="image == null && editedIndex !== -1 && editedItem.image !== ''"
                    :src="apiImage(editedItem.image)"
                  ></v-img>
                </v-avatar>
              </v-col>
            </v-row>

            <v-row align="center" v-if="image != null">
              <v-col class="px-10 pa-lg-5" cols="12" sm="12" md="12">
                <v-avatar size="100%">
                  <v-img v-if="image != null" :src="imageUrl"></v-img>
                </v-avatar>
              </v-col>
            </v-row>
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.name"
                    label="Name"
                    color="primary_red_1"
                    :rules="nameRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-select
                    color="primary_red_1"
                    :items="roles"
                    v-model="editedItem.role"
                    label="Role"
                    :rules="roleRules"
                    item-text="name"
                    return-object
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.email"
                    label="Email"
                    color="primary_red_1"
                    :rules="emailRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.phone"
                    label="Phone"
                    color="primary_red_1"
                    :rules="phoneRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.password"
                    label="Password"
                    color="primary_red_1"
                    :rules="passwordRules"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12" class="pb-1">
                  <v-btn dark color="primary_red_1" @click="onPickFile">image</v-btn>
                  <input type="file" ref="fileInput" style="display:none" @change="onFilePicked" accept="image/*" />
                </v-col>
                <v-col cols="12" sm="12" md="12" class="py-0">
                  <div class="primary_red_1--text caption">
                    {{ imageErrorMessage }}
                  </div>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeEditOrNew">Cancel</v-btn>
          <v-btn color="green darken-1" text @click="save">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Delete Item</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row>
              <div>Are you sure want do delete this item?</div>
              <div class="font-weight-bold">{{ item.name }}</div>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="deleteDialog = false">Cancel</v-btn>
          <v-btn color="red darken-1" @click="confirmDelete" text>Delete</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>
<style>
.tf {
  max-width: 40px;
}
</style>
<script>
import VClamp from 'vue-clamp'
import lazyImg from '../../assets/lazy-img.jpg'
import Navbar from '../../components/Navbar'
import { checkTokenExp, refreshToken } from '../../services'
import Axios from 'axios'
import { apiBaseUrl } from '../../api'
import numberWithPeriod from '../../mixins/numberWithPeriod'
import FormData from 'form-data'
export default {
  mounted() {
    Promise.all([this.loadRole(), this.loadAddress(), this.loadStore(), this.loadCurrentDealer()]).then(() => {
      this.isLoading = false
    })
  },
  data: () => ({
    imageUrl: '',
    image: null,
    valid: true,
    nameRules: [(v) => !!v || 'Name is required'],
    roleRules: [(v) => !!v || 'Role is required'],
    phoneRules: [(v) => !!v || 'Phone is required'],
    passwordRules: [(v) => !!v || 'Password is required'],
    emailRules: [
      (v) => {
        const checkEmail = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/
        return checkEmail.test(v) || 'The email you entered is not valid'
      },
    ],
    userRules: [(v) => !!v || 'User is required'],
    isMainRules: [(v) => !!v || 'Is Main is required'],
    descriptionRules: [(v) => !!v || 'Area is required'],
    imageErrorMessage: '',
    success: false,
    error: false,
    errorMessage: '',
    successMessage: '',
    dialog: false,
    deleteDialog: false,
    isLoading: true,
    skeletonLoaderItem: [0, 1, 2, 3, 4],
    lazyImg: lazyImg,
    headers: [
      { text: 'No', value: 'index' },
      { text: 'Name', value: 'name' },
      { text: 'Role', value: 'role.name' },
      { text: 'Email', value: 'email' },
      { text: 'Phone', value: 'phone' },
      { text: 'Toko', value: 'storeId.name' },
      { text: 'Image', value: 'image' },
      { text: 'Actions', value: 'action', sortable: false },
    ],
    item: {},
    roles: [],
    editedIndex: -1,
    editedItem: {
      _id: '',
      name: '',
      role: null,
      email: '',
      phone: '',
      password: '',
    },
    defaultItem: {
      _id: '',
      name: '',
      role: null,
      email: '',
      phone: '',
      password: '',
    },

    // address
    addresses: [],
    addressHeaders: [
      { text: 'No', value: 'index' },
      { text: 'Name', value: 'name' },
      { text: 'User Id', value: 'userId.name' },
      { text: 'Description', value: 'description' },
      { text: 'Is Main', value: 'isMain' },
      { text: 'Actions', value: 'action', sortable: false },
    ],
    addressEditedIndex: -1,
    addressEditedItem: {
      _id: '',
      name: '',
      userId: null,
      description: '',
      isMain: '',
    },
    addressDefaultItem: {
      _id: '',
      name: '',
      userId: null,
      description: '',
      isMain: '',
    },

    // store
    stores: [],
    storeHeaders: [
      { text: 'No', value: 'index' },
      { text: 'Name', value: 'name' },
      { text: 'User Id', value: 'userId.name' },
      { text: 'Area', value: 'area' },
      { text: 'Latitude', value: 'location.latitude' },
      { text: 'Longitude', value: 'location.longitude' },
      { text: 'Image', value: 'image' },
      { text: 'Actions', value: 'action', sortable: false },
    ],
    storeEditedIndex: -1,
    storeEditedItem: {
      _id: '',
      name: '',
      userId: null,
      area: '',
    },
    storeDefaultItem: {
      _id: '',
      name: '',
      userId: null,
      area: '',
    },

    // current Dealer
    dealer: {},
  }),
  components: {
    Navbar,
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },

  watch: {
    dialog(val) {
      val || this.closeEditOrNew()
    },
  },

  created() {
    // this.initialize();
  },
  methods: {
    async loadCurrentDealer() {
      const requestDealer = async (userId) => {
        const { token } = JSON.parse(localStorage.getItem('auth'))
        const headers = {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json',
        }
        const url = `${apiBaseUrl}/api/users/${userId}`
        const config = {
          headers: headers,
          method: 'GET',
          url: url,
        }
        return await Axios(config)
      }
      const checkToken = await checkTokenExp()
      if (checkToken === true) {
        const { dealerId } = this.$route.params
        const result = await requestDealer(dealerId)
        this.dealer = result.data.user
      }
    },
    async loadStore() {
      const requestStore = async () => {
        const { token } = JSON.parse(localStorage.getItem('auth'))
        const headers = {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json',
        }
        const url = `${apiBaseUrl}/api/stores`
        const config = {
          headers: headers,
          method: 'GET',
          url: url,
        }
        try {
          return await Axios(config)
        } catch (error) {
          throw new Error(error)
        }
      }
      const checkToken = await checkTokenExp()
      if (checkToken === true) {
        const result = await requestStore()
        this.stores = result.data.stores.map((store, index) => ({
          ...store,
          index,
        }))
        // return requestStore();
      }
    },
    validate() {
      return this.$refs.form.validate()
    },
    resetForm() {
      this.$refs.form.reset()
    },
    resetValidation() {
      this.$refs.form.resetValidation()
    },
    resetErrorMessage() {
      this.imageErrorMessage = ''
    },
    onPickFile() {
      this.$refs.fileInput.click()
    },
    emptyFormData() {
      this.image = null
      this.imageUrl = ''
    },
    onFilePicked(event) {
      const files = event.target.files
      let filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        return alert('Please add a valid file!')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.imageUrl = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]

      this.imageErrorMessage = ''
    },
    async loadAddress() {
      const requestAddress = async () => {
        const { token } = JSON.parse(localStorage.getItem('auth'))
        const { dealerId } = this.$route.params
        const headers = {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json',
        }
        const url = `${apiBaseUrl}/api/addresses?userId=${dealerId}`
        const config = {
          headers: headers,
          method: 'GET',
          url: url,
        }
        return await Axios(config)
      }
      const checkToken = await checkTokenExp()
      if (checkToken === true) {
        const result = await requestAddress()

        this.addresses = result.data.addresses.map((address, index) => ({
          ...address,
          isMain: address.isMain.toString(),
          index,
        }))
        // return requestDealer();
      }
    },
    async loadRole() {
      const requestRole = async () => {
        const { token } = JSON.parse(localStorage.getItem('auth'))
        const headers = {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json',
        }
        const url = `${apiBaseUrl}/api/roles`
        const config = {
          headers: headers,
          method: 'GET',
          url: url,
        }
        return await Axios(config)
      }
      const checkToken = await checkTokenExp()
      if (checkToken === true) {
        const result = await requestRole()
        // this.roles = result.data.roles.map((role, index) => ({
        //   ...role,
        //   index,
        // }));
        this.roles = result.data.roles.filter((role) => role.name === 'dealer')
        // return requestDealer();
      }
    },
    successAlert(message) {
      this.successMessage = message
      this.success = true
      setTimeout(() => {
        this.success = false
      }, 5000)
    },
    errorAlert(message) {
      this.errorMessage = message
      this.error = true
      setTimeout(() => {
        this.error = false
      }, 5000)
    },
    apiImage(image) {
      return `${apiBaseUrl}/${image}`
    },
  },
}
</script>
